<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
                placeholder="监测对象、高支模名称、设备编号、区域名称过滤"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="builds"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column prop="siteshortname" label="监测对象">
                </el-table-column>
                <el-table-column
                    prop="bactive"
                    label="是否当前区域"
                    align="center"
                >
                    <template slot-scope="scope">
                        <el-tag
                            effect="dark"
                            :type="
                                scope.row.bactive == '1' ? 'success' : 'danger'
                            "
                            disable-transitions
                            >{{ scope.row.bactive == 1 ? "是" : "否" }}</el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column prop="hfwname" label="高支模名称">
                </el-table-column>
                <el-table-column prop="hfwcode" label="设备编号">
                </el-table-column>
                <el-table-column prop="hfwaname" label="区域名称">
                </el-table-column>

                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
                class="file-dialog"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="监测对象分组" prop="GROUPID">
                                <el-select
                                    v-model="editForm.GROUPID"
                                    filterable
                                    placeholder="请选择"
                                    @change="groupChange"
                                >
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="监测对象" prop="SITEID">
                                <el-select
                                    v-model="editForm.SITEID"
                                    filterable
                                    placeholder="请选择"
                                    @change="siteChange"
                                >
                                    <el-option
                                        v-for="item in selectSites"
                                        :key="item.SITEID"
                                        :label="item.siteshortname"
                                        :value="item.SITEID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="高支模" prop="HFWID">
                                <el-select
                                    v-model="editForm.HFWID"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in hfws"
                                        :key="item.HFWID"
                                        :label="item.hfwname"
                                        :value="item.HFWID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="区域名称" prop="hfwaname">
                                <el-input
                                    v-model="editForm.hfwaname"
                                    auto-complete="off"
                                    maxlength="2    0"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="上传图片" prop="imgurl">
                                <el-upload
                                    ref="imgEditFileUpload"
                                    class="upload-demo"
                                    action="fakeaction"
                                    accept=".jpg"
                                    list-type="picture-card"
                                    :class="{ disabled: uploadEditDisabled }"
                                    :multiple="false"
                                    :limit="1"
                                    :file-list="editForm.imgurl"
                                    :on-change="imgEditFileChanged"
                                    :http-request="uploadEditImgFile"
                                    :on-remove="removeEditImgFile"
                                >
                                    <i slot="default" class="el-icon-plus"></i>
                                    <div slot="file" slot-scope="{ file }">
                                        <el-image
                                            class="
                                                el-upload-list__item-thumbnail
                                            "
                                            :src="file.url"
                                            fit="cover"
                                        ></el-image>
                                        <span
                                            class="el-upload-list__item-actions"
                                        >
                                            <span
                                                class="
                                                    el-upload-list__item-preview
                                                "
                                                @click="handleOpenFile(file)"
                                            >
                                                <i class="el-icon-zoom-in"></i>
                                            </span>
                                            <span
                                                class="
                                                    el-upload-list__item-delete
                                                "
                                                @click="
                                                    handleEditRemoveImgFile(
                                                        file
                                                    )
                                                "
                                            >
                                                <i class="el-icon-delete"></i>
                                            </span>
                                        </span>
                                    </div>
                                </el-upload>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="设为当前区域" prop="bactive">
                                <el-switch
                                    v-model="editForm.bactive"
                                    :active-value="1"
                                    :inactive-value="0"
                                >
                                </el-switch>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
                class="file-dialog"
            >
                <el-form
                    :model="addForm"
                    label-width="120px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="监测对象分组" prop="GROUPID">
                                <el-select
                                    v-model="addForm.GROUPID"
                                    filterable
                                    placeholder="请选择"
                                    @change="groupChange"
                                >
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="监测对象" prop="SITEID">
                                <el-select
                                    v-model="addForm.SITEID"
                                    filterable
                                    placeholder="请选择"
                                    @change="siteChange"
                                >
                                    <el-option
                                        v-for="item in selectSites"
                                        :key="item.SITEID"
                                        :label="item.siteshortname"
                                        :value="item.SITEID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="高支模" prop="HFWID">
                                <el-select
                                    v-model="addForm.HFWID"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in hfws"
                                        :key="item.HFWID"
                                        :label="item.hfwname"
                                        :value="item.HFWID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="区域名称" prop="hfwaname">
                                <el-input
                                    v-model="addForm.hfwaname"
                                    auto-complete="off"
                                    maxlength="20"
                                ></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="上传图片" prop="imgurl">
                                <el-upload
                                    ref="imgFileUpload"
                                    class="upload-demo"
                                    action="fakeaction"
                                    accept=".jpg"
                                    list-type="picture-card"
                                    :class="{ disabled: uploadAddDisabled }"
                                    :multiple="false"
                                    :limit="1"
                                    :file-list="addForm.imgurl"
                                    :on-change="imgFileChanged"
                                    :http-request="uploadImgFile"
                                    :on-remove="removeImgFile"
                                >
                                    <i slot="default" class="el-icon-plus"></i>
                                    <div slot="file" slot-scope="{ file }">
                                        <el-image
                                            class="
                                                el-upload-list__item-thumbnail
                                            "
                                            :src="file.url"
                                            fit="cover"
                                        ></el-image>
                                        <span
                                            class="el-upload-list__item-actions"
                                        >
                                            <span
                                                class="
                                                    el-upload-list__item-preview
                                                "
                                                @click="handleOpenFile(file)"
                                            >
                                                <i class="el-icon-zoom-in"></i>
                                            </span>
                                            <span
                                                class="
                                                    el-upload-list__item-delete
                                                "
                                                @click="
                                                    handleRemoveImgFile(file)
                                                "
                                            >
                                                <i class="el-icon-delete"></i>
                                            </span>
                                        </span>
                                    </div>
                                    <!-- <el-button
                                    v-show="addForm.imgurl.length == 0"
                                    slot="trigger"
                                    plain
                                    type="primary"
                                    >选择文件</el-button
                                >
                                <el-button
                                    v-show="addForm.imgurl.length != 0"
                                    style="margin-left: 10px"
                                    type="danger"
                                    @click="handleRemoveImgFile"
                                    >删除</el-button
                                > -->
                                </el-upload>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设为当前区域" prop="bactive">
                                <el-switch
                                    v-model="addForm.bactive"
                                    active-value="1"
                                    inactive-value="0"
                                >
                                </el-switch>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="addFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    gethfwapagelist,
    gethfwaGroups,
    removehfwa,
    edithfwa,
    addhfwa,
    checkhfwcode,
    gethfws,
    postfile,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
import { mfogtype } from "../../assets/dic/dic";
export default {
    components: { leftgroup, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            builds: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                HFWID: [
                    {
                        required: true,
                        message: "请选择高支模",
                        trigger: "blur",
                    },
                ],
                hfwaname: [
                    {
                        required: true,
                        message: "请填写区域名称",
                        trigger: "blur",
                    },
                ],
                imgurl: [
                    {
                        required: true,
                        message: "请上传图片",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {
                imgurl: [],
            },
            addFormVisible: false,
            addLoading: false,
            addFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                HFWID: [
                    {
                        required: true,
                        message: "请选择高支模",
                        trigger: "blur",
                    },
                ],
                hfwaname: [
                    {
                        required: true,
                        message: "请填写区域名称",
                        trigger: "blur",
                    },
                ],
                imgurl: [
                    {
                        required: true,
                        message: "请上传图片",
                        trigger: "blur",
                    },
                ],
            },
            addForm: {
                imgurl: [],
            },
            canEdit: false,
            canDelete: false,
            groups: [],
            selectSites: [],
            hfws: [],
            selectGroupId: 0,
            mfogtype: mfogtype,
        };
    },
    computed: {
        uploadEditDisabled: function () {
            return this.editForm.imgurl.length > 0;
        },
        uploadAddDisabled: function () {
            return this.addForm.imgurl.length > 0;
        },
    },
    methods: {
        handleRemoveImgFile() {
            this.$refs["imgFileUpload"].clearFiles();
            this.addForm.imgurl = [];
            this.addForm.fileid = "";
        },
        handleEditRemoveImgFile() {
            this.$refs["imgEditFileUpload"].clearFiles();
            this.editForm.imgurl = [];
            this.editForm.fileid = "";
        },
        removeImgFile(file, fileList) {
            this.addForm.imgurl = [];
            this.addForm.fileid = "";
        },
        removeEditImgFile(file, fileList) {
            this.editForm.imgurl = [];
            this.editForm.fileid = "";
        },
        uploadImgFile(param) {
            let fileObj = param.file; // 文件对象
            let form = new FormData(); // FormData 对象
            form.append("formFiles", fileObj);
            postfile(form)
                .then((res) => {
                    this.addForm.fileid = res.data;
                })
                .catch((err) => {});
        },
        uploadEditImgFile(param) {
            let fileObj = param.file; // 文件对象
            let form = new FormData(); // FormData 对象
            form.append("formFiles", fileObj);
            postfile(form)
                .then((res) => {
                    this.editForm.fileid = res.data;
                })
                .catch((err) => {});
        },
        imgFileChanged(file, fileList) {
            this.addForm.imgurl = fileList;
        },
        imgEditFileChanged(file, fileList) {
            this.editForm.imgurl = fileList;
        },
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getPageList();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                groupid: this.selectGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            gethfwapagelist(paras).then((res) => {
                if (res.success) {
                    this.builds = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },
        getGroupCounts() {
            gethfwaGroups().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        groupChange(val) {
            this.getGroupSites(val);
            this.$set(this.addForm, "SITEID", "");
            this.$set(this.editForm, "SITEID", "");
            this.selectSites = this.groupSites;

            this.$set(this.addForm, "HFWID", "");
            this.$set(this.editForm, "HFWID", "");
            this.hfws = [];
        },
        async siteChange(val) {
            this.$set(this.addForm, "HFWID", "");
            this.$set(this.editForm, "HFWID", "");
            this.hfws = [];

            var result = await gethfws({ siteid: val });
            this.hfws = result.data;
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = row;
                    removehfwa(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getPageList(this.page);
                        this.getGroupCounts();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });
                return;
            }

            this.editFormVisible = true;
            this.groupChange(row.GROUPID);
            this.editForm = Object.assign({}, row);
            this.$set(this.editForm, "bactive", row.bactive);
            this.$set(this.editForm, "imgurl", [{ url: row.filepath }]);

            var result = await gethfws({ siteid: row.SITEID });
            this.hfws = result.data;
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = { imgurl: [] };
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    edithfwa(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.editLoading = false;
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;

            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let { imgurl, ...para } = Object.assign({}, this.addForm);

                    addhfwa(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
        handleOpenFile(file) {
            window.open(file.url);
        },
    },
    mounted() {
        this.getPageList();
        this.getGroupCounts();
        this.getGroups();

        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
.file-dialog /deep/ .el-upload-list__item {
    transition: none !important;
}
.disabled /deep/ .el-upload--picture-card {
    display: none !important;
}
.file-dialog /deep/ .el-upload-list__item > div {
    height: 100%;
}
</style>


